<template>
  <div id="chat">
    <beautiful-chat
      class="sidebar"
      ref="chat"
      :title="chatTitle"
      :participants="participants"
      :onMessageWasSent="onMessageWasSent"
      :messageList="messageList"
      :newMessagesCount="newMessagesCount"
      :isOpen="isChatOpen"
      :close="closeChat"
      :open="openChat"
      :showEdition="true"
      :showTypingIndicator="showTypingIndicator"
      :showLauncher="true"
      :showCloseButton="true"
      :colors="colors"
      :alwaysScrollToBottom="alwaysScrollToBottom"
      :messageStyling="messageStyling"
      @onType="handleOnType"
      @edit="editMessage"
    >
          <!-- :icons="icons" -->

      <!-- <template v-slot:user-avatar="{ user }">
        <div class="chat-avartar">
          <img :src="user.imageUrl" alt="">
        </div>
      </template> -->
      <template v-slot:text-message-body="data" class="message-body">
          <div v-if="data.message.author === 'me'" class="message-head-me">{{ data.message.data.userId }}</div>
          <div v-else class="message-head-ot">{{ data.message.data.userId }}</div>
          <div>{{ data.message.data.text }}</div>
      </template>
    </beautiful-chat>
    
  </div>
</template>

<script>
import axios from "axios";
// import VueCookies from "vue-cookies";
import Stomp from "webstomp-client";
import SockJS from "sockjs-client";

// import CloseIcon from "vue-beautiful-chat/src/assets/close-icon.png";
// import OpenIcon from "vue-beautiful-chat/src/assets/logo-no-bg.svg";
// import FileIcon from "vue-beautiful-chat/src/assets/file.svg";
// import CloseIconSvg from "vue-beautiful-chat/src/assets/close.svg";
// const PROFURL = `${process.env.VUE_APP_SERVER_URL}/user/infobyuserno`
 
var reconnect = 0;

export default {
  name: "Chat",
  props: {
    conferenceNo: Number,
    userInfo: Object,
  },
  created() {
    this.classNo = this.$route.params.classno;
    this.userId ="dddfff22";
    this.setChatInit(this.classNo, this.userId)
    console.log("init")
   
    // App.vue가 생성되면 소켓 연결을 시도합니다.
  },
  // updated() {
  //   this.connect(this.chatRoomNo, this.userId)
  // },
  watch: {
    isChatOpen() {
      if (this.isChatOpen)
        this.newMessagesCount = 0
    },
    messageList: {
      deep: true,
      handler() {
        if (!this.isChatOpen)
          this.newMessagesCount += 1
      },
    },
  },
  data() {
    return {
      classNo: "",  
      chatRoomNo:"",
      room: {},
      userId: "",
      // chat ui
      icons: {
        // open: {
        //   img: OpenIcon,
        //   name: "default",
        // },
        // close: {
        //   img: CloseIcon,
        //   name: "default",
        // },
        // closeSvg: {
        //   img: CloseIconSvg,
        //   name: "default",
        // },
      },
      // the list of all the participant of the conversation. `name` is the user name, `id` is used to establish the author of a message, `imageUrl` is supposed to be the user avatar.
      participants: [],
      chatTitle: "Chatting",
      titleImageUrl:
        "https://a.slack-edge.com/66f9/img/avatars-teams/ava_0001-34.png",
      // the list of the messages to show, can be paginated and adjusted dynamically
      messageList: [],
      newMessagesCount: 0,
      isChatOpen: false, // to determine whether the chat window should be open or closed
      showTypingIndicator: "", // when set to a value matching the participant.id it shows the typing indicator for the specific user
      colors: {
        header: {
          bg: "#4DB6AC",
          text: "#ffffff",
        },
        launcher: {
          bg: "#4e8cff",
        },
        messageList: {
          bg: "#ffffff",
        },
        sentMessage: {
          bg: "white",
          text: "black",
        },
        receivedMessage: {
          bg: "white",
          text: "black",
        },
        userInput: {
          bg: "#f4f7f9",
          text: "#565867",
        },
      }, // specifies the color scheme for the component
      alwaysScrollToBottom: false, // when set to true always scrolls the chat to the bottom when new events are in (new message, user starts typing...)
      messageStyling: true, // enables *bold* /emph/ _underline_ and such (more info at github.com/mattezza/msgdown)
    };
  },
  methods: {
    setParticipants() {
      const participant = {}
      participant.id = this.userInfo.userId
      participant.name = this.userInfo.name ? this.userInfo.name : this.userInfo.userId
      participant.imageUrl = `data:image/jpg;base64,${this.userInfo.profileImg}`

      this.participants.push(participant)
      
    },
    onMessageWasSent(message) {
      // called when the user sends a message
      // 기존 코드
      // 밑에 꺼 사용하면, 두번 입력됨.
      // this.messageList = [...this.messageList, JSON.parse(JSON.stringify(message))];
      message.author = this.userId
      this.send(message)
    },
    send(message) {
      console.dir(message);
      this.stompClient.send("/pub/chat/message", 
        JSON.stringify({
            chatType:2,
            chatRoomNo: this.chatRoomNo,
            userId: this.userId,
            content: message.data.text,
          })
        )
    },
    openChat() {
      // called when the user clicks on the fab button to open the chat
      this.isChatOpen = true;
      this.newMessagesCount = 0;
    },
    closeChat() {
      // called when the user clicks on the botton to close the chat
      this.isChatOpen = false;
    },
    handleScrollToTop() {
      // called when the user scrolls message list to top
      // leverage pagination for loading another page of messages
    },
    handleOnType() {
      // 누군가 타자칠 때 나오는 문구
      // console.log("Emit typing event");
    },
    editMessage(message) {
      const m = this.messageList.find((m) => m.id === message.id);
      m.isEdited = true;
      m.data.text = message.data.text;
    },
    async findRoomS(classNo) {
      await this.findRoom(classNo)
    },
    setChatInit(classNo, userId) {
      this.findRoomS(classNo)
      this.connect(classNo, userId)
    },
    // socket start
    findRoom(classNo) {
      axios
        .get(
          `/chatroom/class?classNo=` +
            classNo
        )
        .then((response) => {
          this.room = response.data.object;
        });
    },
    connect(classNo, userId) {
      let sock = new SockJS(`http://localhost:9999/dodam/ws-stomp`);
      this.stompClient = Stomp.over(sock);
      this.stompClient.debug = () => {};

      // let ppl = this.participants     // 참여자 리스트
      // pub/sub event
      this.stompClient.connect(
        {},
        frame => {
          // frame;
          this.connected = true;
          this.stompClient.subscribe(
            "/sub/chatroom/class?classNo=" + classNo
            , chat => {
              var recv = JSON.parse(chat.body);
              const checkParticipant = this.participants.some(p => p.id === recv.userId)
              if(recv.chatType === 1 && checkParticipant){frame}
              else
              {
                this.messageList.push({
                  type: "text",
                  author: recv.userId === userId ? "me" : recv.userId ,
                  data: { text: `${recv.content}`, userId: `${recv.userId}` },
                });
              }

              // if (!checkParticipant)
              // {
              //   const participant = {}
              //   axios
              //     .get(`http://localhost:9999/dodam/user/infobyuserid?userId=${recv.userId}`)
              //     .then(res => {
              //       participant.id = recv.userId
              //       participant.name = recv.userId
              //       participant.imageUrl = `data:image/jpg;base64,${res.data.object.profileImg}`
              //       this.participants.push(participant)
              //     })  
              // }
            }
          );
          
          this.stompClient.send(
            "/pub/chat/message",
            // 입장체크 chatType: 1
            JSON.stringify({ chatType:1, chatRoomNo: this.chatRoomNo, userNo:2,userId:this.userId })
          );
        },
        // 재연결 함수. start
        function () {
          if (reconnect++ <= 5) {
            setTimeout(function () {
              console.log("connection reconnect");
              sock = new SockJS(`http://localhost:9999/dodam/ws-stomp`);
              this.stompClient = Stomp.over(sock);
              this.connect();
            }, 1000);
          }
        }
        // 재연결 함수. end
      );
    },
  },
  mounted() {
    // this.connect(this.chatRoomNo, this.userId);
  },
};
</script>

<style scoped>
.sidebar {
  position: absolute;
  z-index: 9999;
}

.chat-avartar {
  border-radius:50%; 
  font-size: 15px; 
  line-height:25px; 
  width: 25px !important; 
  height: 25px !important; 
  min-width: 25px;
  min-height: 25px;
  margin: 1px;
}

.message-head-me {
  font-weight: bold;
  text-align: right;
}

.message-head-ot {
  font-weight: bold;
  text-align: lef;
}

.sc-chat-window::-webkit-scrollbar {
  width: 10px;
}

.sc-chat-window::-webkit-scrollbar-thumb {
  background-color: #4db6ac;
  border-radius: 10px;
  background-clip: padding-box;
  border: 2px solid transparent;
}

.sc-chat-window::-webkit-scrollbar-track {
  background-color: #c8e6c9;
  border-radius: 10px;
  box-shadow: inset 0px 0px 5px white;
}
</style>
